<script>
import { CaixaService } from '../../../services'
const caixaService = new CaixaService();

export default {
    data(){
        return{
            receita: [],
            despesa: [],
            timer:null,
            buscando: false,
            anos: [new Date().getFullYear()],
            meses: [
                { mes: 1, descricao: 'Janeiro'},
                { mes: 2, descricao: 'Fevereiro'},
                { mes: 3, descricao: 'Março'},
                { mes: 4, descricao: 'Abril'},
                { mes: 5, descricao: 'Maio'},
                { mes: 6, descricao: 'Junho'},
                { mes: 7, descricao: 'Julho'},
                { mes: 8, descricao: 'Agosto'},
                { mes: 9, descricao: 'Setembro'},
                { mes: 10, descricao: 'Outubro'},
                { mes: 11, descricao: 'Novembro'},
                { mes: 12, descricao: 'Dezembro'},
            ],
            filtro:{
                ano: new Date().getFullYear(),
                mes: null,
                estilo: 'simplificado',

                detalhes_conta_pagar_entrantes: false,
                detalhes_conta_pagar_acumulado: false,
                detalhes_conta_receber_entrantes: false,
                detalhes_conta_receber_acumulado: false,
                
                filtro_conta_pagar_entrantes: 'TODOS',
                filtro_conta_receber_entrantes: 'TODOS',

                nivel: 4
            },
            dados: null
        }   
    },
    
    methods: {
        debounceBusca () {
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarDados();
            }, 800);
        },
        
        buscarDados () {
            if(this.buscando)
                return
            this.buscando = true;
            let f = JSON.parse(JSON.stringify(this.filtro));
            f.mes = f.mes.mes
            caixaService.fechamentoMensal(f).then( response => {
                    let ret = response.data;

                        ret.contas_receber_entrantes = ret.contas_receber_entrantes.map(c => { 
                            c.valor_total = Number(c.valor_total)
                            c.valor_pago = Number(c.valor_pago)
                            c.saldo = c.valor_total - c.valor_pago

                            return c
                        });

                        ret.contas_pagar_entrantes = ret.contas_pagar_entrantes.map(c => { 
                            c.valor_total = Number(c.valor_total)
                            c.valor_pago = Number(c.valor_pago)
                            c.saldo = c.valor_total - c.valor_pago

                            return c
                        });

                        ret.contas_receber_acumulado = ret.contas_receber_acumulado.map(c => { 
                            c.valor_total = Number(c.valor_total)
                            c.valor_pago = Number(c.valor_pago)
                            c.saldo = c.valor_total - c.valor_pago

                            return c
                        });

                        ret.contas_pagar_acumulado = ret.contas_pagar_acumulado.map(c => { 
                            c.valor_total = Number(c.valor_total)
                            c.valor_pago = Number(c.valor_pago)
                            c.saldo = c.valor_total - c.valor_pago

                            return c
                        });

                        this.dados = ret
                        
                 
                }
            ).finally( () => this.buscando = false)
        },


        buscarAnos () {
            caixaService.buscarAnos().then(
                response => {
                   this.anos = response.data
                }
            )
        },

        imprimir_fechamento_mensal (){
            let f = JSON.parse(JSON.stringify(this.filtro));
            f.mes = f.mes.mes
            let url = caixaService.link_fechamento_mensal(f);
            return url
            // window.open(url,'_blank');
        }
    },
    computed:{
        contas_pagar_entrantes_total () {
            if(!this.dados.contas_pagar_entrantes)
                return 0;
            return this.dados.contas_pagar_entrantes.reduce( (a,c) => { return a + c.valor_total }, 0)
        },
        contas_pagar_entrantes_pago () {
            if(!this.dados.contas_pagar_entrantes)
                return 0;
            return this.dados.contas_pagar_entrantes.reduce( (a,c) => { return a + c.valor_pago }, 0)
        },

        contas_pagar_entrantes_saldo () {
            return (this.contas_pagar_entrantes_total - this.contas_pagar_entrantes_pago)
        },

        contas_pagar_acumulado_total () {
            if(!this.dados.contas_pagar_acumulado)
                return 0;
            return this.dados.contas_pagar_acumulado.reduce( (a,c) => { return a + c.valor_total }, 0)
        },
        contas_pagar_acumulado_pago () {
            if(!this.dados.contas_pagar_acumulado)
                return 0;
            return this.dados.contas_pagar_acumulado.reduce( (a,c) => { return a + c.valor_pago }, 0)
        },

        contas_pagar_acumulado_saldo () {
            return (this.contas_pagar_acumulado_total - this.contas_pagar_acumulado_pago)
        },

        contas_receber_entrantes_total () {
            if(!this.dados.contas_receber_entrantes)
                return 0;
            return this.dados.contas_receber_entrantes.reduce( (a,c) => { return a + c.valor_total }, 0)
        },
        contas_receber_entrantes_pago () {
            if(!this.dados.contas_receber_entrantes)
                return 0;
            return this.dados.contas_receber_entrantes.reduce( (a,c) => { return a + c.valor_pago }, 0)
        },

        contas_receber_entrantes_saldo () {
            return (this.contas_receber_entrantes_total - this.contas_receber_entrantes_pago)
        },

        contas_receber_acumulado_total () {
            if(!this.dados.contas_receber_acumulado)
                return 0;
            return this.dados.contas_receber_acumulado.reduce( (a,c) => { return a + c.valor_total }, 0)
        },
        contas_receber_acumulado_pago () {
            if(!this.dados.contas_receber_acumulado)
                return 0;
            return this.dados.contas_receber_acumulado.reduce( (a,c) => { return a + c.valor_pago }, 0)
        },

        contas_receber_acumulado_saldo () {
            return (this.contas_receber_acumulado_total - this.contas_receber_acumulado_pago)
        },


        saldo_caixas () {
            if(!this.dados.caixas)
                return 0
            return this.dados.caixas.reduce( (a,c) => { return a + Number(c.saldo[0].saldo)}, 0);
        },

        saldo_caixas_atual () {
            if(!this.dados.caixas)
                return 0
            return this.dados.caixas.reduce( (a,c) => { return a + Number(c.saldo_atual[0].saldo)}, 0);
        },
        
        total_saldo(){
             if(!this.dados.dre)
                return 0
            
            let receitas = (this.dados.dre.receita[0].credito - this.dados.dre.receita[0].debito);
            let despesas = (this.dados.dre.despesa[0].debito - this.dados.dre.despesa[0].credito);
            
            return (receitas - despesas);
        },

        filtro_conta_pagar_entrantes(){
            if(this.filtro.filtro_conta_pagar_entrantes === 'TODOS')
                return this.dados.contas_pagar_entrantes

            return this.dados.contas_pagar_entrantes.filter(x => {
                if (this.filtro.filtro_conta_pagar_entrantes === 'QUITADO')
                    return x.status == 'QUITADO'
                
                return x.saldo > 0
            })
        },

        filtro_conta_receber_entrantes(){
            if(this.filtro.filtro_conta_receber_entrantes === 'TODOS')
                return this.dados.contas_receber_entrantes

            return this.dados.contas_receber_entrantes.filter(x => {
                if (this.filtro.filtro_conta_receber_entrantes === 'QUITADO')
                    return x.status == 'QUITADO'
                
                return x.saldo > 0
            })
        },
        

    },
    beforeMount() {
        this.filtro.mes = this.meses[new Date().getMonth()]
    },
    mounted(){        
        this.buscarAnos();
        setTimeout(() => {
            this.buscarDados();
        }, 200);
    },
};
</script>
